 */\n\nh1 {\n    font-size: 2em;\n    margin: 0.67em 0;\n}\n\n/* Grouping content\n     ========================================================================== */\n\n/**\n   * 1. Add the correct box sizing in Firefox.\n   * 2. Show the overflow in Edge and IE.\n   */\n\nhr {\n    box-sizing: content-box;\n    /* 1 */\n    height: 0;\n    /* 1 */\n    overflow: visible;\n    /* 2 */\n}\n\n/**\n   * 1. Correct the inheritance and scaling of font size in all browsers.\n   * 2. Correct the odd `em` font sizing in all browsers.\n   */\n\npre {\n    font-family: monospace, monospace;\n    /* 1 */\n    font-size: 1em;\n    /* 2 */\n}\n\n/* Text-level semantics\n     ========================================================================== */\n\n/**\n   * Remove the gray background on active links in IE 10.\n   */\n\na {\n    background-color: transparent;\n}\n\n/**\n   * 1. Remove the bottom border in Chrome 57-\n   * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.\n   */\n\nabbr[title] {\n    border-bottom: none;\n    /* 1 */\n    text-decoration: underline;\n    /* 2 */\n    text-decoration: underline dotted;\n    /* 2 */\n}\n\n/**\n   * Add the correct font weight in Chrome, Edge, and Safari.\n   */\n\nb,\nstrong {\n    font-weight: bolder;\n}\n\n/**\n   * 1. Correct the inheritance and scaling of font size in all browsers.\n   * 2. Correct the odd `em` font sizing in all browsers.\n   */\n\ncode,\nkbd,\nsamp {\n    font-family: monospace, monospace;\n    /* 1 */\n    font-size: 1em;\n    /* 2 */\n}\n\n/**\n   * Add the correct font size in all browsers.\n   */\n\nsmall {\n    font-size: 80%;\n}\n\n/**\n   * Prevent `sub` and `sup` elements from affecting the line height in\n   * all browsers.\n   */\n\nsub,\nsup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n}\n\nsub {\n    bottom: -0.25em;\n}\n\nsup {\n    top: -0.5em;\n}\n\n/* Embedded content\n     ========================================================================== */\n\n/**\n   * Remove the border on images inside links in IE 10.\n   */\n\nimg {\n    border-style: none;\n}\n\n/* Forms\n     ========================================================================== */\n\n/**\n   * 1. Change the font styles in all browsers.\n   * 2. Remove the margin in Firefox and Safari.\n   */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n    font-family: inherit;\n    /* 1 */\n    font-size: 100%;\n    /* 1 */\n    line-height: 1.15;\n    /* 1 */\n    margin: 0;\n    /* 2 */\n}\n\n/**\n   * Show the overflow in IE.\n   * 1. Show the overflow in Edge.\n   */\n\nbutton,\ninput {\n    /* 1 */\n    overflow: visible;\n}\n\n/**\n   * Remove the inheritance of text transform in Edge, Firefox, and IE.\n   * 1. Remove the inheritance of text transform in Firefox.\n   */\n\nbutton,\nselect {\n    /* 1 */\n    text-transform: none;\n}\n\n\n/**\n   * Remove the inner border and padding in Firefox.\n   */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n    border-style: none;\n    padding: 0;\n}\n\n/**\n   * Restore the focus styles unset by the previous rule.\n   */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n    outline: 1px dotted ButtonText;\n}\n\n/**\n   * Correct the padding in Firefox.\n   */\n\nfieldset {\n    padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n   * 1. Correct the text wrapping in Edge and IE.\n   * 2. Correct the color inheritance from `fieldset` elements in IE.\n   * 3. Remove the padding so developers are not caught out when they zero out\n   *    `fieldset` elements in all browsers.\n   */\n\nlegend {\n    box-sizing: border-box;\n    /* 1 */\n    color: inherit;\n    /* 2 */\n    display: table;\n    /* 1 */\n    max-width: 100%;\n    /* 1 */\n    padding: 0;\n    /* 3 */\n    white-space: normal;\n    /* 1 */\n}\n\n/**\n   * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n   */\n\nprogress {\n    vertical-align: baseline;\n}\n\n/**\n   * Remove the default vertical scrollbar in IE 10+.\n   */\n\ntextarea {\n    overflow: auto;\n}\n\n/**\n   * 1. Add the correct box sizing in IE 10.\n   * 2. Remove the padding in IE 10.\n   */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n    box-sizing: border-box;\n    /* 1 */\n    padding: 0;\n    /* 2 */\n}\n\n/**\n   * Correct the cursor style of increment and decrement buttons in Chrome.\n   */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n    height: auto;\n}\n\n/**\n   * 1. Correct the odd appearance in Chrome and Safari.\n   * 2. Correct the outline style in Safari.\n   */\n\n[type=\"search\"] {\n    /* 1 */\n    outline-offset: -2px;\n    /* 2 */\n}\n\n/**\n   * Remove the inner padding in Chrome and Safari on macOS.\n   */\n\n[type=\"search\"]::-webkit-search-decoration {\n    -webkit-appearance: none;\n}\n\n/**\n   * 1. Correct the inability to style clickable types in iOS and Safari.\n   * 2. Change font properties to `inherit` in Safari.\n   */\n\n::-webkit-file-upload-button {\n    -webkit-appearance: button;\n    /* 1 */\n    font: inherit;\n    /* 2 */\n}\n\n/* Interactive\n     ========================================================================== */\n\n/*\n   * Add the correct display in Edge, IE 10+, and Firefox.\n   */\n\ndetails {\n    display: block;\n}\n\n/*\n   * Add the correct display in all browsers.\n   */\n\nsummary {\n    display: list-item;\n}\n\n/* Misc\n     ========================================================================== */\n\n/**\n   * Add the correct display in IE 10+.\n   */\n\ntemplate {\n    display: none;\n}\n\n/**\n   * Add the correct display in IE 10.\n   */\n\n[hidden] {\n    display: none;\n}","%flex-between {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: space-between;\n}\n\n%flex-center {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  justify-content: center;\n}\n","html {\n  box-sizing: border-box;\n  scroll-behavior: smooth;\n  font-size: 62.5%;\n  width: 100%;\n  overflow-x: hidden;\n}\n\n*,\n*::after,\n*::before {\n  box-sizing: inherit;\n}\n\nbody {\n  margin: 0 auto;\n  padding: 0;\n  font-family: $font-family-main;\n  @include font($font-size-base, $line-height-base, $font-weight-normal);\n}\n\nul,\np,\nh1,\nh2,\nh3,\nh4 {\n  margin: 0;\n}\n\nh1 {\n  @include font($font-size-h1, $line-height-h1);\n  color: $color-gray-medium;\n\n  @include media-laptop {\n    @include font($font-size-h1-laptop, $line-height-h1);\n  }\n\n  @include media-tablet {\n    @include font($font-size-h1-tablet, $line-height-h1-tablet);\n  }\n}\n\nh2 {\n  font-family: $font-family-secondary;\n  @include font($font-size-h2, $line-height-h2, $font-weight-normal);\n\n  @include media-laptop {\n    @include font(\n      $font-size-h2-laptop,\n      $line-height-h2-laptop,\n      $font-weight-normal\n    );\n  }\n\n  @include media-mobile {\n    @include font(\n      $font-size-h2-mobile,\n      $line-height-h2-mobile,\n      $font-weight-normal\n    );\n  }\n}\n\nh3 {\n  @include font($font-size-h3, $line-height-h3);\n  @include media-mobile {\n    @include font($font-size-h3-mobile, $line-height-h3);\n  }\n}\n\nh4 {\n  @include font($font-size-h4, $line-height-h4, $font-weight-normal);\n}\n\nh5 {\n  @include font($font-size-h5, $line-height-h5);\n}\n\nul {\n  list-style: none;\n  padding: 0;\n}\n\na {\n  color: $color-black;\n  text-decoration: none;\n}\n\nbutton {\n  border: 1px solid transparent;\n  cursor: pointer;\n}\n\nbutton:disabled {\n  opacity: 0.6;\n  cursor: not-allowed;\n}\n\nbutton:active {\n  transform: translateY(1px);\n}\n\n:focus-visible {\n  outline: $outline-width solid rgba($outline-color-rgb, $outline-opacity);\n  outline-offset: $outline-offset;\n}\n\n:focus:not(:focus-visible) {\n  outline: none;\n}\n","// Typography\n$font-family-main: \"Inter\", Verdana, sans-serif;\n$font-family-secondary: \"Inika\", \"Bitstream Vera Serif\", serif;\n\n// Font sizes\n$font-size-base: 1.6rem;\n$font-size-small: 1.2rem;\n$font-size-h1: 4.5rem;\n$font-size-h2: 4rem;\n$font-size-h3: 2rem;\n$font-size-h4: 2rem;\n$font-size-h5: 1.2rem;\n\n// Line heights\n$line-height-base: 2rem;\n$line-height-h1: 5rem;\n$line-height-h2: 5.2rem;\n$line-height-h3: 2rem;\n$line-height-h4: 4rem;\n$line-height-h5: 2rem;\n\n// Font weights\n$font-weight-normal: normal;\n$font-weight-bold: bold;\n\n// Responsive typography\n$font-size-h1-laptop: 3.5rem;\n$font-size-h1-tablet: 2.5rem;\n\n$font-size-h2-laptop: 3.5rem;\n$font-size-h2-mobile: 2.5rem;\n\n$font-size-h3-mobile: 1.5rem;\n\n$line-height-h1-laptop: 5rem;\n$line-height-h1-tablet: 3.5rem;\n\n$line-height-h2-laptop: 4.5rem;\n$line-height-h2-mobile: 3.257rem;\n","@mixin flex-column($align, $display: flex, $direction: column, $wrap: wrap) {\n  display: $display;\n  flex-wrap: $wrap;\n  flex-direction: $direction;\n  align-items: $align;\n}\n\n@mixin font($size, $height, $weight: $font-weight-bold, $style: normal) {\n  font-weight: $weight;\n  font-size: $size;\n  line-height: $height;\n  font-style: $style;\n}\n\n@mixin columns($width) {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: space-between;\n\n  > * {\n    flex-basis: $width;\n  }\n}\n\n@mixin grid-template-columns($cols, $rows, $col-gap, $row-gap) {\n  display: -ms-grid;\n  display: grid;\n  -ms-grid-columns: $cols;\n  grid-row: $rows;\n  grid-template-columns: repeat($cols, 1fr);\n  grid-template-rows: repeat($rows, 1fr);\n  grid-column-gap: $row-gap;\n  grid-row-gap: $col-gap;\n}\n\n@mixin media-large-laptop {\n  @media screen and (max-width: $content-width) {\n    @content;\n  }\n}\n\n@mixin media-laptop {\n  @media screen and (max-width: $laptop-width) {\n    @content;\n  }\n}\n\n@mixin media-tablet {\n  @media screen and (max-width: $tablet-width) {\n    @content;\n  }\n}\n\n@mixin media-mobile {\n  @media screen and (max-width: $mobile-width) {\n    @content;\n  }\n}\n","// Text colors\n$color-green-primary: #499a18;\n$color-orange-accent: #e06733;\n$color-gray-dark: #636060;\n$color-gray-medium: #717171;\n$color-off-white: #e3e1d5;\n$color-black: #000000;\n$color-dark-green: #010201;\n\n// Background\n$color-background-light: #edf2ec;\n$color-light: #fff;\n","// Buttons\n// Btn color\n$color-accordion-btn-active: #8ba07e;\n$color-accordion-btn-inactive: #d9d9d9;\n$color-btn-inactive: #aea1a1;\n$color-btn-active: #8ba07e;\n//Btn size\n$button-width: 16.1rem;\n$button-height: 5rem;\n$button-border-radius: 0.5rem;\n// Btn more size\n$button-more-width: 15.8rem;\n$button-more-height: 4.4rem;\n$button-more-border-radius: 1rem;\n//Btn size adoptive\n$button-mobile-width: 12.1rem;\n$button-mobile-height: 3.5rem;\n$button-more-mobile-width: 10rem;\n$button-more-mobile-height: 3.4rem;\n\n// Outline\n$outline-color: #8ba07e;\n$outline-color-rgb: rgb(139, 160, 126);\n$outline-opacity: 0.4;\n$outline-width: 2px;\n$outline-offset: 2px;\n",".button {\n  width: $button-width;\n  height: $button-height;\n  font-family: $font-family-main;\n  @extend %flex-center;\n\n  @include font(2rem, 2.6rem, 400);\n  border: 1px solid $color-orange-accent;\n  border-radius: $button-border-radius;\n  transition: all 0.3s ease-in-out;\n\n  @media (hover: hover) {\n    &:hover {\n      box-shadow: $color-gray-medium 0.3rem 0.3rem 1rem;\n    }\n  }\n\n  &:active {\n    box-shadow: $color-gray-medium 0.3rem 0.3rem 1rem;\n  }\n\n  @include media-mobile {\n    @include font(1.5rem, 1.9rem, 400);\n    width: $button-mobile-width;\n    height: $button-mobile-height;\n  }\n\n  // Learn more modification\n  &--more {\n    width: $button-more-width;\n    height: $button-more-height;\n    @include font(1.6rem, 2rem, 400);\n    background-color: $color-background-light;\n    border: $color-orange-accent 0.2rem solid;\n    color: $color-black;\n    text-transform: uppercase;\n    border-radius: $button-more-border-radius;\n\n    @media (hover: hover) {\n      &:hover {\n        color: $color-light;\n        background-color: $color-orange-accent;\n      }\n    }\n\n    &:active {\n      color: $color-light;\n      background-color: $color-orange-accent;\n    }\n\n    @include media-mobile {\n      width: $button-more-mobile-width;\n      height: $button-more-mobile-height;\n      @include font(1rem, 2rem, 400);\n    }\n  }\n\n  // Contact us modification\n  &--contact {\n    background-color: $color-light;\n    color: $color-orange-accent;\n    font-weight: bold;\n    font-family: \"Inika\";\n\n    @media (hover: hover) {\n      &:hover {\n        color: $color-light;\n        background-color: $color-orange-accent;\n      }\n    }\n\n    &:active {\n      color: $color-light;\n      background-color: $color-orange-accent;\n    }\n\n    @include media-laptop {\n      margin-bottom: $prices-button-margin-bottom-laptop;\n    }\n\n    @include media-mobile {\n      margin-bottom: 0;\n    }\n  }\n}\n","/* inter-regular - variable */\n$font-path: \"/src/fonts/\";\n\n@font-face {\n  font-family: \"Inter\";\n  src: url(\"#{$font-path}Inter.var.woff2\") format(\"woff2-variations\");\n  font-weight: 100 900;\n  font-display: swap;\n}\n\n/* inika-regular - latin */\n@font-face {\n  font-family: \"Inika\";\n  font-style: normal;\n  font-weight: 400;\n  src: url(\"#{$font-path}inika-v21-latin-regular.woff2\") format(\"woff2\");\n  font-display: swap;\n}\n\n/* inika-700 - latin */\n@font-face {\n  font-family: \"Inika\";\n  font-style: normal;\n  font-weight: 700;\n  src: url(\"#{$font-path}inika-v21-latin-700.woff2\") format(\"woff2\");\n  font-display: swap;\n}\n",".wrapper {\n  max-width: $content-width;\n  margin: auto;\n  padding: $wrapper-padding-desktop;\n\n  @include media-laptop {\n    padding: $wrapper-padding-laptop;\n  }\n\n  @include media-tablet {\n    padding: $wrapper-padding-tablet;\n  }\n}\n","// Main breakpoints\n$content-width: 1440px;\n$laptop-width: 1024.98px;\n$tablet-width: 767.98px;\n$mobile-width: 425.98px;\n\n//additional breakpoints\n$footer-tablet-breakpoint: 900px;\n$dropdown-tablet-breakpoint: 500px;\n\n// Grid columns\n$column-4-width: 21%;\n$column-3-width: 30%;\n$column-2-width: 45%;\n$column-2-width-laptop: 52%;\n\n// Container\n$wrapper-padding-desktop: 0 11rem;\n$wrapper-padding-laptop: 0 5rem;\n$wrapper-padding-tablet: 0 2.5rem;\n",".header {\n  background-color: $color-background-light;\n  padding: $header-padding-desktop 0;\n  transition: padding 0.3s ease;\n\n  @include media-laptop {\n    padding: $header-padding-laptop 0;\n  }\n\n  &__overlay {\n    visibility: hidden;\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background-color: $color-gray-medium;\n\n    &--active {\n      visibility: visible;\n      opacity: $overlay-opacity;\n      transition-delay: 0s;\n    }\n  }\n\n  &__container {\n    @extend %flex-between;\n  }\n\n  &__nav {\n    @include media-tablet {\n      position: fixed;\n      top: 0;\n      right: 0;\n      width: $nav-tablet-width;\n      height: 100%;\n      background-color: $color-light;\n      z-index: 5;\n      padding-top: $nav-padding-top;\n      transform: translateX(100%);\n      transition: all 0.8s;\n    }\n\n    &--active {\n      transform: translateX(0);\n    }\n  }\n}\n\n// Class for body to prevent scrolling when mobile menu is open\n.hidden {\n  overflow: hidden;\n  position: fixed;\n}\n","//Menu\n$nav-link-gap: 4.723rem;\n$nav-link-gap-tablet: 1rem;\n$nav-tablet-width: 32rem;\n$nav-padding-top: 15rem;\n$overlay-opacity: 0.5;\n\n$line-height-nav: 5rem;\n$font-size-nav-tablet: 2.5rem;\n$line-height-nav-tablet: 3rem;\n\n// Logo\n\n$logo-margin-bottom: 0.45rem;\n\n// Burger\n$color-burger: #717171;\n$burger-width: 40px;\n$burger-height: 24px;\n$burger-line-height: 2px;\n$burger-transition-time: 0.3s;\n$burger-line-radius: 3px;\n",".footer {\n  background-color: $color-light;\n  padding: $footer-desktop-padding;\n\n  @include media-laptop {\n    padding: $footer-laptop-padding;\n  }\n\n  @include media-mobile {\n    padding: $footer-mobile-padding;\n  }\n\n  // Main container\n  &__container {\n    @extend %flex-between;\n    flex-wrap: nowrap;\n  }\n\n  // Copyright section\n  &__copyright {\n    display: flex;\n    align-items: center;\n    gap: $copyright-gap-desktop;\n    flex-wrap: nowrap;\n\n    &-text {\n      @include font(2rem, 5rem, normal);\n\n      @include media-tablet {\n        @include font(2rem, 2.6rem, normal);\n      }\n    }\n\n    &-link {\n      display: flex;\n      align-items: center;\n      white-space: nowrap;\n      transition: all 0.3s ease-in-out;\n\n      @media (hover: hover) {\n        &:hover {\n          opacity: 0.7;\n        }\n      }\n\n      &:active {\n        opacity: 0.7;\n      }\n\n      @include media-tablet {\n        font-size: 0;\n      }\n    }\n\n    &-icon {\n      height: $copyright-icon-height-desktop;\n      width: auto;\n      vertical-align: middle;\n      margin-right: $copyright-icon-margin;\n\n      @include media-tablet {\n        margin-right: 0;\n      }\n    }\n  }\n\n  // Social links\n  &__links {\n    @extend %flex-between;\n    justify-content: flex-end;\n    gap: $links-gap-desktop;\n\n    @include media-tablet {\n      gap: $links-gap-tablet;\n    }\n\n    @include media-mobile {\n      gap: $links-gap-mobile;\n    }\n\n    &-item img {\n      transition: all 0.3s ease-in-out;\n\n      @media (hover: hover) {\n        &:hover {\n          opacity: 0.7;\n        }\n      }\n\n      &:active {\n        opacity: 0.7;\n      }\n\n      @include media-mobile {\n        width: auto;\n        height: $copyright-icon-height-mobile;\n      }\n    }\n  }\n}\n","// Header\n$header-padding-desktop: 4.2rem;\n$header-padding-laptop: 3.6rem;\n\n// About section\n$about-desktop-padding: 9.4rem 0;\n$about-laptop-padding: 4rem 0 9.5rem;\n$about-mobile-padding: 3rem 0;\n$about-heading-margin-desktop: 6.6rem 0 4rem;\n$about-heading-margin-laptop: 0 0 5.2rem;\n$about-heading-margin-mobile: 0 0 1.6rem;\n$about-text-margin-laptop: 0 0 7rem;\n$about-image-max-width: 38%;\n$about-image-max-width-laptop: 37%;\n$about-image-max-width-mobile: 44%;\n\n// Service section\n$service-desktop-padding: 4rem 0 12rem;\n$service-laptop-padding: 6rem 0 7rem;\n$service-mobile-padding: 3rem 0 2.7rem;\n$service-wrapper-margin-bottom: 8rem;\n$service-wrapper-margin-bottom-laptop: 4rem;\n$service-heading-margin-bottom-laptop: 4rem;\n$service-buttons-gap-laptop: 3.8rem;\n$service-buttons-gap-tablet: 1rem;\n\n$service-wrapper-max-width: 100.7rem;\n\n$service-heading-max-width: 30rem;\n$service-heading-max-width-mobile: 20rem;\n\n$service-button-width-tablet: 12.1rem;\n$service-button-height-tablet: 3.5rem;\n\n$service-gallery-padding-mobile: 0 2.5rem;\n\n//grid params\n$grid-column-gap-desktop: 5.5rem;\n$grid-row-gap-desktop: 11.5rem;\n$grid-column-gap-laptop: 3.8rem;\n$grid-row-gap-laptop: 10.6rem;\n$grid-column-gap-tablet: 3.8rem;\n$grid-row-gap-tablet: 4rem;\n$grid-column-gap-mobile: 3.2rem;\n$grid-row-gap-mobile: 0;\n$grid-item-border-radius: 2rem;\n$grid-item-hover-shadow: 0.5rem 0.5rem 1rem;\n$grid-item-hover-scale: 1.01;\n$grid-item-border-radius: 2rem;\n$grid-item-hover-shadow: 0.5rem 0.5rem 1rem;\n$grid-item-hover-scale: 1.01;\n\n$service-image-height: 35rem;\n$service-image-border-radius-top: 2rem 2rem 0 0;\n$service-image-border-radius-bottom: 0 0 2rem 2rem;\n\n$description-padding-desktop: 0 1rem;\n$description-padding-laptop: 0 3.8rem;\n$service-pick-name-margin: 1rem 0;\n$service-pick-description-margin-bottom: 3.6rem;\n$service-pick-description-margin-bottom-laptop: 2rem;\n\n// Prices section\n$prices-desktop-padding: 4rem 0 11rem;\n$prices-laptop-padding: 4rem 0 8.5rem;\n$prices-mobile-padding: 2.5rem 0 4rem;\n$prices-heading-margin: 0 0 5.5rem;\n$prices-heading-padding-left: 1rem;\n$prices-heading-margin-mobile: 0 0 3.5rem;\n$prices-rightside-max-width-desktop: 42.5rem;\n$prices-rightside-max-width-laptop: 30rem;\n$prices-rightside-gap: 4rem;\n$prices-rightside-margin-top-laptop: 10rem;\n$prices-rightside-margin-top-tablet: 4rem;\n$prices-rightside-margin-top-mobile: 1.9rem;\n$prices-subheading-margin-bottom-laptop: 2.6rem;\n$prices-subheading-margin-bottom-mobile: 1.8rem;\n$prices-button-margin-bottom-laptop: 3.4rem;\n$prices-image-max-width: 16rem;\n\n// Contacts section\n$contacts-desktop-padding-top: 5.3rem;\n$contacts-laptop-padding-top: 4rem;\n$contacts-mobile-padding: 2.5rem 1.5rem 0;\n$contacts-laptop-gap: 25rem;\n$contacts-mobile-gap: 13.2rem;\n$contacts-image-width: 33.2%;\n$contacts-image-width-laptop: 30%;\n$contacts-image-width-mobile: 33.4%;\n$contacts-heading-margin: 0.7rem 0 3.4rem;\n$contacts-heading-margin-laptop: 0 0 10rem;\n$contacts-heading-margin-mobile: 0 0 4rem;\n$contacts-wrapper-width-desktop: 58%;\n$contacts-wrapper-width-laptop: 67.4%;\n$contacts-wrapper-width-mobile: 100%;\n$contacts-button-width: 122px;\n$contacts-button-height: 26px;\n$contacts-button-padding: 0.6rem 0;\n\n// Footer\n$footer-desktop-padding: 4rem 0;\n$footer-laptop-padding: 3.3rem 0 2rem;\n$footer-mobile-padding: 2.2rem 0 1rem;\n\n$copyright-gap-desktop: 2rem;\n$copyright-gap-mobile: 1rem;\n$links-gap-desktop: 2.4rem;\n$links-gap-tablet: 2.7rem;\n$links-gap-mobile: 1.2rem;\n\n$copyright-icon-height-desktop: 32px;\n$copyright-icon-height-mobile: 24px;\n$copyright-icon-margin: 5px;\n",".layout-4-columns {\n  @include columns($width: $column-4-width);\n}\n\n.layout-3-columns {\n  @include columns($width: $column-3-width);\n}\n\n.layout-2-columns {\n  @include columns($width: $column-2-width);\n  @include media-laptop {\n    @include columns($width: $column-2-width-laptop);\n  }\n}\n",".logo {\n  display: flex;\n  align-items: center;\n  margin-bottom: $logo-margin-bottom;\n\n  @include media-mobile {\n    margin: 0;\n  }\n\n  &__image {\n    cursor: pointer;\n    transition: all 0.3s ease-in-out;\n\n    @media (hover: hover) {\n      &:hover {\n        filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.2));\n      }\n    }\n\n    &:active {\n      filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.2));\n    }\n  }\n}\n","// Menu styles\n.menu {\n  @extend %flex-between;\n  @include font($font-size-base, $line-height-nav, $font-weight-normal);\n  gap: $nav-link-gap;\n  list-style: none;\n  font-family: \"Inika\";\n  margin: 0;\n\n  @include media-tablet {\n    @include flex-column(center);\n    gap: $nav-link-gap-tablet;\n  }\n\n  &__link {\n    color: $color-dark-green;\n    text-decoration: none;\n    transition: all 0.3s ease-in-out;\n\n    @media (hover: hover) {\n      &:hover {\n        color: $color-orange-accent;\n        font-weight: $font-weight-bold;\n      }\n    }\n\n    &:active {\n      color: $color-orange-accent;\n      font-weight: $font-weight-bold;\n    }\n\n    @include media-tablet {\n      @include font(\n        $font-size-nav-tablet,\n        $line-height-nav-tablet,\n        $font-weight-normal\n      );\n    }\n  }\n}\n",".burger {\n  display: none;\n  position: relative;\n  width: $burger-width;\n  height: $burger-height;\n  cursor: pointer;\n  z-index: 10;\n\n  @include media-tablet {\n    display: block;\n  }\n\n  &::before,\n  &::after {\n    content: \"\";\n    background-color: $color-burger;\n    display: block;\n    position: absolute;\n    left: 0;\n    height: $burger-line-height;\n    width: 100%;\n    border-radius: $burger-line-radius;\n    transition: transform $burger-transition-time ease,\n      opacity $burger-transition-time ease;\n  }\n\n  &::before {\n    top: 0;\n  }\n\n  &::after {\n    bottom: 0;\n  }\n\n  &__line {\n    background-color: $color-burger;\n    display: block;\n    position: absolute;\n    left: 0;\n    height: $burger-line-height;\n    width: 100%;\n    border-radius: $burger-line-radius;\n    top: 50%;\n    transform: translateY(-50%);\n    transition: transform $burger-transition-time ease,\n      opacity $burger-transition-time ease;\n  }\n\n  &__move {\n    position: fixed;\n    top: 3.6rem;\n    right: 2.5rem;\n    background-color: transparent;\n\n    .burger__line {\n      opacity: 0;\n    }\n\n    &::after {\n      transform: rotate(45deg);\n      bottom: calc($burger-height / 2.4);\n    }\n\n    &::before {\n      transform: rotate(-45deg);\n      top: calc($burger-height / 2.4);\n    }\n  }\n}\n",".icon {\n  background: transparent;\n  &__circle {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    width: $icon-circle-size;\n    height: $icon-circle-size;\n    border-radius: $icon-circle-radius;\n    background-color: $color-btn-inactive;\n\n    &--light {\n      background-color: $color-accordion-btn-inactive;\n    }\n\n    @include media-mobile {\n      margin-right: $icon-mobile-margin;\n    }\n  }\n\n  &__arrow {\n    display: block;\n    width: $icon-arrow-width;\n    height: $icon-arrow-height;\n    background-image: url(\"../img/svg/arrow-light.svg\");\n\n    &--dark {\n      background-image: url(\"../img/svg/arrow-dark.svg\");\n    }\n  }\n}\n","// Icon sizes\n$icon-circle-size: 16px;\n$icon-circle-radius: 10px;\n$icon-arrow-width: 6px;\n$icon-arrow-height: 4px;\n$icon-mobile-margin: 0.9rem;\n",".accordion {\n  width: $accordion-width;\n  border: 1px solid $color-off-white;\n  border-radius: $accordion-border-radius;\n  padding: $accordion-padding;\n  @include flex-column(center);\n  gap: $accordion-gap;\n\n  @include media-mobile {\n    margin: 0 auto;\n    padding: $accordion-mobile-padding;\n    width: $accordion-mobile-width;\n    gap: $accordion-mobile-gap;\n  }\n\n  &__plan-button {\n    @extend %flex-between;\n    background-color: $color-accordion;\n    padding: $accordion-item-padding;\n    border-radius: $accordion-border-radius;\n    width: 100%;\n    cursor: pointer;\n    transition: all 0.3s ease-in-out;\n\n    &[hidden] {\n      display: none;\n    }\n\n    &[aria-expanded=\"true\"] {\n      .icon__arrow {\n        transform: rotate(180deg);\n      }\n    